@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@700;800;900&family=Overlock:wght@700;900&display=swap');
html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    position: relative;
}

* {
    box-sizing: border-box;
}

*:before, *:after {
    box-sizing: border-box;
}

.App {
    min-height: 100vh;
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 100px;
}

footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}


